.Navbar {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 1em;
    margin-bottom: 2em;
    background-color: rgb(192, 45, 26);
    color: #fff;
}
.NavContent {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-items: space-around;
}
.nav-link {
    display: inline-block;
    padding: 0.5em 1em;
    color: rgba(255, 255, 255, 0.835);
    font-weight: bold;
    text-decoration: none;
}
.nav-link-active {
    color: #fff;
    border-bottom: 2px solid;
}